*, :before, :after {
  box-sizing: border-box;
}

body {
  color: var(--gray-0);
  font-size: var(--size-5);
  font-family: var(--font-sans);
  background-color: var(--gray-12);
  place-content: center;
  margin: 0;
  display: grid;
}

h1 {
  justify-content: center;
  align-items: flex-end;
  display: flex;
}

.version {
  font-weight: bold;
}

.container {
  margin: 10px;
}

.pbw-demo {
  font-size: 15px;
}

#search-bar {
  color: #fff;
  background-color: #333;
  border: none;
  border-radius: 5px;
  outline: none;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
}

#search-bar::placeholder {
  color: #888;
}

.btn-link {
  color: #fff;
  text-align: center;
  background-color: #333;
  border: 1px solid #444;
  border-radius: 5px;
  text-decoration: none;
  transition: background-color .3s, border-color .3s;
}

.btn-link:hover {
  background-color: #444;
  border-color: #555;
}

dialog::backdrop {
  background: #00000080;
}

#pbw-dialog {
  background-color: var(--gray-12);
  width: 100%;
  max-width: 640px;
  height: 100%;
  max-height: 360px;
  padding: 0;
  overflow: visible;
}

.dialog-close-btn {
  z-index: 1;
  text-align: center;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  line-height: 30px;
  position: absolute;
  top: -15px;
  right: 0;
}

#examples {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.example-btn {
  width: 100%;
  margin: 10px;
  padding: 20px 0;
}

